.sf_card {
  width: 100%;
  background-color: #fff;
  border: 0.5px solid #cacaca;
  border-radius: 6px;
  color: #1c023b;
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  margin-top: 0.5vw;
  padding: 1vw;
  position: relative;
}
.sf_card_hover {
  width: 100%;
  background-color: #fff;
  border: 0.5px solid #cacaca;
  border-radius: 6px;
  color: #1c023b;
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  margin-top: 0.5vw;
  padding: 1vw;
  position: relative;
  transition: all ease-in-out 200ms 50ms;
}
.iadcc_card_hover {
  width: 100%;
  background-color: #fff;
  color: #1c023b;
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  padding: 0.9rem 1rem;
  position: relative;
  border-bottom: 0.5px solid rgba(0, 0, 0, 0.15);
  transition: all ease-in-out 200ms 50ms;
}
.sf_card_contatiner {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 1rem;
}
.sf_card_contatiner > img {
  width: 3rem;
  height: 3rem;
  border-radius: 50%;
}

.sf_card_contatiner_inner > h6,
.cedc_input_conatiner > h6 {
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  color: #121212;
  margin-bottom: 0.2rem;
}
.sf_card_contatiner_inner > p {
  font-weight: 400;
  font-size: 0.878vw;
  line-height: 1.098vw;
  color: rgba(0, 0, 0, 0.6);
  margin-bottom: 0;
  white-space: break-spaces;
}
.sf_card_active,
.sf_card_hover:hover,
.iadcc_card_hover:hover {
  background-color: rgba(75, 26, 133, 0.1);
}
/* // for question style  */
.ifq_card {
  width: 100%;
  background-color: #fff;
  border: 0.5px solid #cacaca;
  border-radius: 6px;
  box-shadow: 0px 1px 3px rgb(0 0 0 / 40%);
  margin-block: 0.8rem;
  padding: 0.8rem 1rem;
}
.ifq_card_inner {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  gap: 1rem;
}
.ifq_card_inner > h6 {
  margin: 0;
  font-weight: var(--font-4);
  font-size: var(--font-13);
  color: var(--t-head-color);
  white-space: break-spaces;
}
.ifqo_cantainer {
  margin: 0.5rem;
}
.ifqo_cantainer > h6 {
  margin: 0;
  font-weight: var(--font-4);
  font-size: var(--font-13);
  color: var(--t-head-color);
  white-space: break-spaces;
  min-width: 10rem;
  background-color: #fff;
  border: 0.5px solid #cacaca;
  padding: 0.4rem 1rem;
  margin-bottom: 0.6rem;
  border-radius: 6px;
}

.ifqo_cantainer_student {
  margin: 0.5rem;
}
.ifqo_cantainer_student > h6 {
  margin: 0;
  font-weight: var(--font-4);
  font-size: var(--font-13);
  color: var(--t-head-color);
  white-space: break-spaces;
  min-width: 10rem;
  border: 0.5px solid #cacaca;
  padding: 0.4rem 1rem;
  margin-bottom: 0.6rem;
  border-radius: 6px;
  cursor: pointer;
}
.ifqo_cantainer_student > h6:hover {
  background-color: rgba(75, 26, 133, 0.1);
}
.ifqocs_active {
  background-color: rgba(75, 26, 133, 0.1);
  border: 0.5px solid #1c023b;
}

/* // for style of one staff analytic */
.osa_card {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border: 0.5px solid #cacaca;
  border-top: none;
  height: 3rem;
}
.osa_card > h6 {
  font-weight: var(--font-5);
  font-size: var(--font-14);
  color: var(--t-head-color);
  margin: 0;
  text-align: center;
  border-right: 0.5px solid #cacaca;
  padding: 0.5rem 0.5rem;
  width: var(--sc-width);
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}
/* // for student side give feedback  */
.gsf_contatiner {
  width: 100%;
  background-color: #fff;
  border: 0.5px solid #cacaca;
  border-radius: 6px;
  box-shadow: 0px 1px 3px rgb(0 0 0 / 20%);
  margin-block: 0.8rem;
  padding: 0.8rem 1rem;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 1rem;
}
.gsf_contatiner > img {
  width: 4rem;
  height: 4rem;
  border-radius: 12px;
}
.gsf_star_container {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 1rem;
}
.gsf_star_container > img {
  width: 2rem;
  height: 2rem;
  cursor: pointer;
}

/* // for site info  */
.igc_card {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-wrap: wrap;
  gap: 1%;
}

.igc_card > img {
  width: 24%;
}
.sfcc_input {
  text-align: center;
  height: 100%;
  width: 3.95vw;
  display: grid;
  place-items: center;
  border-radius: 0.5vw;
  background: #d9d9d9;
  font-size: 1rem;
  border: 0.5px solid #cacaca;
}
/* // for continuous evaluation  */
.cedc_input_conatiner {
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* // for inward/outward  */

.iowc_container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 1rem;
}
.iowc_container > img {
  width: 2.5rem;
  height: 2.5rem;
}
.iowc_container_inner > h6 {
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  color: #121212;
  margin-bottom: 0.2rem;
  white-space: break-spaces;
}
.iowc_container_inner > p {
  font-weight: 400;
  font-size: 0.878vw;
  line-height: 1.098vw;
  color: rgba(0, 0, 0, 0.6);
  margin-bottom: 0;
  white-space: break-spaces;
}
.iowc_pdf_icon {
  position: absolute;
  top: 10%;
  right: 2%;
  cursor: pointer;
  height: 1.5rem;
  width: 1.5rem;
}
